<script>
import Atropos from 'atropos';
import {mapState, mapMutations} from 'vuex';
export default {
    data() {
        return {
            atroposInstances: [] 
        };
    },
    computed: {
        ...mapState(['responsive'])
    },

    
    mounted() {
        this.$nextTick(() => {
            this.comprobarResponsive();
            window.addEventListener('resize', this.comprobarResponsive);

            const elements = document.querySelectorAll('.atroposElement');
            elements.forEach(el => {
                const instance = Atropos({ 
                    el: el,
                    activeOffset: 0,
                    shadow: false,
                    shadowOffset: false,
                    shadowScale: false,
                    highlight: false,
                    rotateXMax: 5,
                    rotateYMax: 5,
                    rotateLock: false,
                    commonOrigin: false
                });
                this.atroposInstances.push(instance);
            });
            // console.log(this.atroposInstances);
        });
    },
    beforeUnmount() { 
        window.removeEventListener('resize', this.comprobarResponsive);
        this.atroposInstances.forEach(instance => {
            instance.destroy();
        });
    },
    methods: {
        ...mapMutations(['ESTADO_RESPONSIVE']),
        comprobarResponsive(){
            
            const comprobar = window.innerWidth <= 1050; 
            this.ESTADO_RESPONSIVE(comprobar)
        },
    }
}
</script>

<style lang="scss" scoped>

    atropos-component{
        width: 23%;
    }


    .ocean {
        height: 40%;
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        overflow: hidden;
    }

    .waves {
        position:absolute;
        width: 100%;
        height:15vh;
        min-height:100px;
        max-height:150px;
        bottom: 0;
    }

    .content {
        position:relative;
        height:20vh;
        bottom: 0;
        background-color: rgb(0, 0, 0);
    }

    .parallax > use {
        animation: move-forever 5s cubic-bezier(.55,.5,.45,.5) infinite;
    }
    .parallax > use:nth-child(1) {
        animation-delay: -2s;
        animation-duration: 11s;
    }
    .parallax > use:nth-child(2) {
        animation-delay: -3s;
        animation-duration: 10s;
    }
    .parallax > use:nth-child(3) {
        animation-delay: -4s;
        animation-duration: 8s;
    }
    .parallax > use:nth-child(4) {
        animation-delay: -5s;
        animation-duration: 6s;
    }

    @keyframes move-forever {
    0% {
        transform: translate3d(-90px,0,0);
    }
    100% { 
        transform: translate3d(85px,0,0);
    }
    }

    @media (max-width: 768px) {
    .waves {
        height:40px;
        min-height:40px;
    }
    .content {
        height:30vh;
    }
    h1 {
